.TimelineTrack {
    display: -ms-flexbox;
    display: flex;
    height: 50px;
    background-size: contain;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.TimelineTrack__CropHandle {
    display: inline-block;
    height: 100%;
    background: rgba(255,255,255,0.666);
    width: 25px;
    -ms-flex-negative: 0;
        flex-shrink: 0;
    -ms-flex-positive: 0;
        flex-grow: 0;
}

.TimelineTrack__CropHandle:hover {
    background: white;
}

.TimelineTrack__CropHandle:active {
    background: #cdcdcd;
}

.TimelineTrack__CropHandle.left {
    cursor: e-resize;
    border-left: 1px solid #cdcdcd;
}

.TimelineTrack__CropHandle.right {
    cursor: w-resize;
    border-right: 1px solid #cdcdcd;
}

.TimelineTrack__NudgeHandle {
    display: inline-block;
    -ms-flex-positive: 1;
        flex-grow: 1;
    cursor: -webkit-grab;
    cursor: grab;
}

.TimelineTrack__NudgeHandle:hover {
    background: rgba(255,255,255,0.2);
}

.TimelineTrack__NudgeHandle:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
    background: rgba(0,0,0,0.2);
}


